import { useState,useCallback } from 'react';
import {Link} from "react-router-dom";

const Nav = () => {

    const [plusA,setPlusA] = useState("+");
    const [plusB,setPlusB] = useState("+");
    const [plusC,setPlusC] = useState("+");
    const [plusD,setPlusD] = useState("+");
    const [plusE,setPlusE] = useState("+");

    const plusHandlerA = useCallback(()=>{
        plusA === "+" ? setPlusA("-") : setPlusA("+")
    },[plusA])
    const plusHandlerB = useCallback(()=>{
        plusB === "+" ? setPlusB("-") : setPlusB("+")
    },[plusB])
    const plusHandlerC = useCallback(()=>{
        plusC === "+" ? setPlusC("-") : setPlusC("+")
    },[plusC])
    const plusHandlerD = useCallback(()=>{
        plusD === "+" ? setPlusD("-") : setPlusD("+")
    },[plusD])
    const plusHandlerE = useCallback(()=>{
        plusE === "+" ? setPlusE("-") : setPlusE("+")
    },[plusE])

    return (
        <ul className="mainmenu">
            <li><Link to="/introduction">About us</Link></li>
            <li><Link to="/typeofvisa"></Link></li>
            {/*
            <li className="has-droupdown"><Link to="#"> </Link>
                {<ul className="submenu">
                    <li><Link to="/typeofvisa"> </Link></li>
                    <li><Link to="/employmentvisa"></Link></li>
                    <li><Link to="/investmentvisa"></Link></li>
                    <li><Link to="/residencevisa"> / </Link></li>
                    <li><Link to="/marriagevisa"> / </Link></li>
                    <li><Link to="/overseaskoreansvisa"></Link></li>
                </ul>}
            </li>
             */}

            <li className="has-droupdown" onClick={plusHandlerA}><Link to="#"><span className='mobile_header'> {plusA} </span></Link>
                <ul className="submenu">
                    <li><Link to="/entrance"></Link></li>
                    <li><Link to="/d4">D-4 ()</Link></li>
                    <li><Link to="/d2">D-2 ()</Link></li>
                </ul>
            </li>

            <li className="has-droupdown" onClick={plusHandlerB}><Link to="#"> <span className='mobile_header'> {plusB} </span></Link>
                <ul className="submenu">
                    <li><Link to="/illegalitylitigation">  </Link></li>
                    <li><Link to="/immigrationlitigation"> </Link></li>
                    <li><Link to="/protectionlitigation"> </Link></li>
                </ul>
            </li>

            <li className="has-droupdown" onClick={plusHandlerC}><Link to="#"><span className='mobile_header'> {plusC} </span></Link>
                <ul className="submenu">
                    <li><Link to="/criminallitigation"> </Link></li>
                    <li><Link to="/civillitigation"> </Link></li>
                    <li><Link to="/familylitigation"> </Link></li>
                    <li><Link to="/administrativelitigation"> </Link></li>
                </ul>
            </li>

            <li className="has-droupdown" onClick={plusHandlerD}><Link to="#"> <span className='mobile_header'> {plusD} </span></Link>
                <ul className="submenu">
                    <li><Link to="/residentregistration"></Link></li>
                    <li><Link to="/myselfregistration"> </Link></li>
                    <li><Link to="/parentalregistration"> </Link></li>
                    <li><Link to="/descendantregistration"> </Link></li>
                    <li><Link to="/orphanregistration">, </Link></li>
                    <li><Link to="/onotherregistration">  </Link></li>
                </ul>
            </li>

            <li className="has-droupdown" onClick={plusHandlerE}><Link to="#"><span className='mobile_header'> {plusE} </span></Link>
                <ul className="submenu">
                    <li><Link to="/visainquiry"> </Link></li>
                    <li><Link to="/workinquiry"> </Link></li>
                    <li><Link to="/agencyinquiry"> / </Link></li>
                    <li><Link to="/Price"></Link></li>
                    <li><Link to="/paidinquiry"></Link></li>
                </ul>
            </li>
        </ul>
    )
}
export default Nav;
